.errorBarActive {
  transform: scale(1);
}

.errorBar {
  z-index: 1400;
  position: fixed;
  display: flex;
  -webkit-box-pack: start;
  justify-content: flex-start;
  -webkit-box-align: center;
  align-items: center;

  margin: 16px 0 0;

  bottom: 24px;
  left: 24px;
  right: auto;

  opacity: 1;
  /*transform: none;*/
  transition:
    opacity 225ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,
    transform 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  transform: scale(0);

  .errorBarContent {
    border-radius: 4px;
    box-shadow:
      rgba(0, 0, 0, 0.2) 0 3px 5px -1px,
      rgba(0, 0, 0, 0.14) 0 6px 10px 0,
      rgba(0, 0, 0, 0.12) 0 1px 18px 0;
    /*font-family: Roboto, Helvetica, Arial, sans-serif;*/
    /*font-weight: 500;*/
    /*font-size: 0.875rem;*/
    /*line-height: 1.43;*/
    /*letter-spacing: 0.01071em;*/
    /*background-color: rgb(46, 125, 50);*/
    background-color: red;
    display: flex;
    align-items: center;
    padding: 6px 16px;
    color: rgb(255, 255, 255);
    width: 100%;
  }
}
